<template>
    <div class="login-wrap">
        <div class="ms-login">
            <div class="ms-title">新物流管车平台</div>
            <el-form :model="login_form" :rules="login_rules" ref="login_form" label-width="0px" class="ms-content">
                <el-form-item prop="user_name">
                    <el-input v-model="login_form.user_name" placeholder="请输入用户名" @keyup.enter.native="submitForm('login_form')">
                        <el-button slot="prepend" icon="iconfont icon-wode"></el-button>
                    </el-input>
                </el-form-item>
                <el-form-item prop="user_pass">
                    <el-input type="password" placeholder="请输入密码" v-model="login_form.user_pass" @keyup.enter.native="submitForm('login_form')">
                        <el-button slot="prepend" icon="iconfont icon-mima"></el-button>
                    </el-input>
                </el-form-item>
                <div class="login-btn">
                    <el-button type="primary" @click="submitForm('login_form')">登录</el-button>
                </div>
                <p class="login-tips"></p>
            </el-form>
        </div>
    </div>
</template>

<script>
    export default {
        data: function(){
            return {
                login_form: {
                    user_name: 'admin',
                    user_pass: '123456'
                },
                login_rules: {
                    user_name: [
                        { required: true, message: '请输入用户名', trigger: 'blur' }
                    ],
                    user_pass: [
                        { required: true, message: '请输入密码', trigger: 'blur' }
                    ]
                }
            }
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        /*localStorage.setItem('user',this.login_form.user_name);
                        this.$router.push('/');*/
                        this.$store.dispatch('LoginByUsername', this.login_form).then(() => {
                            this.$router.push({ path: '/' })
                        }).catch(() => {

                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
        }
    }
</script>

<style scoped>
    .login-wrap{
        position: relative;
        width:100%;
        height:100%;
        background-image: url(../assets/login-bg.jpg);
        background-size: 100%;
    }
    .ms-title{
        width:100%;
        line-height: 50px;
        text-align: center;
        font-size:20px;
        color: #fff;
        border-bottom: 1px solid #ddd;
    }
    .ms-login{
        position: absolute;
        left:50%;
        top:50%;
        width:350px;
        margin:-190px 0 0 -175px;
        border-radius: 5px;
        background: rgba(255,255,255, 0.3);
        overflow: hidden;
    }
    .ms-content{
        padding: 30px 30px;
    }
    .login-btn{
        text-align: center;
    }
    .login-btn button{
        width:100%;
        height:36px;
        margin-bottom: 10px;
    }
    .login-tips{
        font-size:12px;
        line-height:30px;
        color:#fff;
    }
</style>